<template>
  <div class="WorkFlowInfo" @click="show">
    <div class="Title">{{ info.WorkflowName }}</div>
    <div class="LastUpdateTime">{{ new Date(info.LastUpdateTime).toLocaleString() }}</div>
  </div>

  <el-dialog v-model="dialogFormVisible" title="工作流信息" width="500">
    <el-form :model="form" label-width="auto">
      <el-form-item label="工作流名称" label-position="right">
        <el-input v-model="Info.WorkflowName" autocomplete="off" />
      </el-form-item>
      <el-form-item label="说明" label-position="right">
        <el-input
          v-model="Info.Description"
          show-word-limit
          maxlength="128"
          type="textarea"
        ></el-input>
      </el-form-item>
    </el-form>
    <template #footer>
      <div class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取消</el-button>
        <el-button type="primary" @click="saveInfo">保存</el-button>
      </div>
    </template>
  </el-dialog>
</template>

<script>
import { ref } from "vue";
//import { notify } from "@kyvg/vue3-notification";
export default {
  name: "WorkFlowInfo",
  props: {
    node: Object,
    workflowID: String,
    isVisible: Boolean,
    info: Object,
  },
  setup() {
    const dialogFormVisible = ref(false);

    return { dialogFormVisible };
  },
  data() {
    return {
      Info: this.info,
    };
  },
  mounted() {},
  methods: {
    show() {
      this.dialogFormVisible = !this.dialogFormVisible;
    },
    saveInfo() {
      this.$emit("save");
      this.dialogFormVisible = false;
      //notify({ type: "warn", title: "Warn", text: "" });
    },
  },
};
</script>
